body {
  transition: 0.3s;
  &.no-scroll {
    overflow: hidden;
  }
  &.opacity-layer {
    position: relative;
    transition: 0.3s;

    &:after {
      content: '';
      background-color: rgb(9, 36, 77);
      opacity: 0.902;
      position: absolute;
      left: 0px;
      top: 0px;
      width: 100%;
      height: 100%;
      z-index: 2;
      transition: 0.3s;
    }    
  }
}

